* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

$white: #fff;
$black: #2e2e2e;
$gray: #787878;
$highlight: #ea8478;
$footer: #f7f7f7;

body {
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
  line-height: 1.5;
  color: $gray;
  background: $white;
  text-rendering: optimizeLegibility;
  height: 100%;
}

//  links and lists section

nav {
  position: sticky;
  top: 0;
  left: 0;
  z-index: 1;
  background: $white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: rgba(0, 0, 0, .1) 1px solid;
  padding: 1rem 4rem;

  @media (max-width: 500px) {
    flex-direction: column;
    padding: 1rem;
  }

  a {
    color: $gray;
    text-decoration: none;
    transition: all 0.2s ease;
    font-size: 1.25rem;
    margin: 0 0.5rem;

    &:hover {
      color: $black;
    }

    &.active {
      color: $black;
    }
  }

  h1 {
    font-size: 2.5rem;
    color: $highlight;
  }
}

// Gallery section

main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  @media screen and (min-width: 1024px) {
    flex-direction: row;
  }
}

.gallery {
  width: 100%;
  padding: 3em 1em;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  .img__container {
    margin-top: 4rem;

    @media screen and (min-width: 1024px) {
      justify-content: flex-start;
      flex-direction: row;
    }
  }

  img {
    width: 100%;
    border-radius: 5px;
    box-shadow: 1px 2px 3px rgba(0, 0, 0, .2);

  }
}



.about {
  display: flex;
  justify-content: center;
  font-size: 1.25rem;
}

footer {
  display: flex;
  justify-content: center;
  padding: 1em;
  position: sticky;
  bottom: 0;
  left: 0;
  z-index: 1;
  background: $white;
  border-top: rgba(0, 0, 0, .1) 1px solid;

  .github__social {
    height: 40px;
    width: auto;
    box-shadow: none;
    opacity: 0.5;
    margin: 0 0.5em;
  }
}